<template>
  <div class="follow">
    <!-- 交给组件跳转 -->
    <UcCell
      v-for="(item,index) of follow"
      :key="item._id"
      :data="item"
      :data-name="'follow'"
      :index="index"
    />
  </div>
</template>

<script>
  import UcCell from '../components/uc-cell.vue'
  import {mapActions,mapState} from 'vuex'
  import {FOLLOW} from '../store/types.js'
  export default {
    name:'follow',
    data(){return {
    }},
    components:{
      UcCell
    },
    mounted(){
      this[FOLLOW]();//mapActions 里面的字符 UPDATE_FOLLOW  ~~ methods内部的函数
    },
    updated(){},
    methods:mapActions([
      FOLLOW
    ]),
    /* methods:{
      UPDATE_FOLLOW(){
        this.$store.dispatch(FOLLOW)
      }
    } */
    computed:mapState([
      'follow'
    ])
  }
</script>

